.compose {
  /* display: flex; */
  position: absolute;
  bottom: 0;
  right: 30px;
  min-width: 500px;
  border-radius: 10px 10px 0px 0px;
  overflow: hidden;
  box-shadow: black;
  background-color: #fff;
}

.compose .compose__header {
  display: flex;
  justify-content: space-between;
  background-color: #333;
  color: #fff;
  align-items: center;
  padding: 7px 15px;
}

.compose__header__left span {
  font-size: 14px;
}

.compose__header__right {
  display: flex;
  align-items: center;
}

.compose__header__left {
  display: flex;
  align-items: center;
}

.compose .compose__header__right svg {
  font-size: 18px;
  margin-left: 10px;
  transition: 0.2s linear 0s;
  padding: 3px;
}

.compose .compose__header__right svg:nth-child(2) path {
  transform: rotate(45deg);
  transform-origin: center;
}

.compose .compose__header__right svg:hover {
  background-color: #545454;
  cursor: pointer;
}

.compose__body {
  display: flex;
}
.compose__body__form {
  display: flex;
  flex-direction: column;
}

.compose__body__form input,
textarea {
  width: 100%;
  height: 40px;
  outline: 0;
  border: 0px;
  box-shadow: 0 -1px 0 0 rgb(100 121 143/12%);
  padding: 0px 10px;
}

.compose__body__form textarea {
  height: auto;
}

.compose__footer {
  display: flex;
  justify-content: space-between;
  padding: 12px 10px;
}

.compose__footer__left button {
  display: flex;
  align-items: center;
  cursor: pointer;
  border: 0px;
  background-color: #297be6;
  outline: 0;
  color: #fff;
  font-size: 15px;
  border-radius: 4px;
  letter-spacing: 1px;
}

.compose__footer__left button svg {
  border-left: 1px solid lightgray;
  margin-left: 15px;
  padding: 7px 2px 7px 2px;
}
.compose__footer__right svg {
  font-size: 20px;
  margin-left: 10px;
  border-radius: 3px;
  color: gray;
  padding: 3px;
  transition: 0, 2 linear 0s;
  cursor: pointer;
}
.compose__footer__right svg:hover {
  background-color: #eee;
  color: #000;
}
